___CSS_LOADER_EXPORT___.push([module.id, `:root {
    box-sizing: border-box;
    --main-color: rgb(151, 191, 255);
    --main-color-darker: rgb(120, 172, 255);
    --font-color-menu: white;
    --checkbox-checked-color: rgb(255, 132, 18);
    --secondary-color: white;
    --secondary-color-darker: rgb(242, 241, 241);
}

body {
    display: grid;
    grid-template-columns: 200px 1fr;
    grid-template-rows: 1fr;
    height: 100vh;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

#menu {
    display: grid;
    grid-template-columns: 200px;
    grid-template-rows: 50px 40px 40px 40px;
    /* place-items: center; */
    justify-content: left;
    grid-column: 1;
    background-color: var(--main-color);
    box-shadow: 4px 0 10px rgba(0, 0, 0, 0.4);
    z-index: 999;
}

#menu>.dashboard {
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 10px auto;
    background-color: var(--main-color);
    height: 50px;
    /* box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4); */
    border-bottom: 1px solid var(--font-color-menu);
    z-index: 999;
}

#menu>.dashboard>#dash {
    padding: 0px 6px;
    font-weight: 600;
    color: var(--font-color-menu);
    font-size: 1.5rem;
}

#menu>div {
    height: 40px;
}

#menu>div>button {
    width: 200px;
    text-align: left;
    border: none;
    height: 40px;
    padding: 0px 6px;
    background-color: var(--main-color);
    color: var(--font-color-menu);
    font-weight: 600;
    font-size: 1rem;
}

#menu>div>button:hover {
    cursor: pointer;
    /* box-shadow: 0 0 4px rgba(0, 0, 0, 0.4); */
    background-color: var(--main-color-darker);
}



#menu>div>button>i {
    margin-right: 5px;
}



#content {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 50px max-content;
    grid-column: 2;
    background-color: var(--main-color);
    z-index: 1;
    padding: 50px;
    row-gap: 30px;
}

#content>* {
    border-radius: 5px;
}

#content>.titleDiv {
    background-color: white;
    padding: 10px 0 10px 15px;
    display: flex;
    justify-content: left;
    align-items: center;
    width: clamp(200px, 250px, 300px);
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);

}

#content>.titleDiv>.title {
    font-size: 1.1rem;
    font-weight: 600;
}

#content>.container {
    background-color: white;
    width: clamp(500px, 600px, 1fr);
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
}

#content>.container>#containerHeaderRow,
#content>.container>.item{
    display: grid;
    grid-template-columns: 1fr 2fr 1fr 1fr .5fr;
    grid-template-rows: auto;
    height: 54px;
    align-items: center;
    border-bottom: 1px solid var(--main-color);
}

#content>.container>#containerHeaderRow{
    border: none;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    font-weight: 600;
    z-index: 600;
}

#content>.container>#containerHeaderRow:hover{
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

#content>.container>.item{
    z-index: 500;
}

#content>.container>.item:hover{
    background-color: var(--secondary-color-darker);
    cursor: pointer;
    z-index: 400;
}

#content>.container>.item>.checkboxDiv>.fa-regular.fa-circle {
    color: var(--checkbox-checked-color);
}

#content>.container>.item>.checkboxDiv>.fa-solid.fa-circle-check {
    color: var(--checkbox-checked-color);
}

#content>.container>#containerHeaderRow>div:nth-child(1),
#content>.container>.item>div:nth-child(1){
    border-right: 1px solid var(--main-color);
    padding: 0 15px 0 15px;
}

#content>.container>#containerHeaderRow>div:nth-child(2),
#content>.container>.item>div:nth-child(2){
    border-right: 1px solid var(--main-color);
    padding: 0 15px 0 15px;
}

#content>.container>#containerHeaderRow>div:nth-child(3),
#content>.container>.item>div:nth-child(3){
    border-right: 1px solid var(--main-color);
    padding: 0 15px 0 15px;
}

#content>.container>#containerHeaderRow>div:nth-child(4),
#content>.container>.item>div:nth-child(4){
    border-right: 1px solid var(--main-color);
    padding: 0 15px 0 15px;
}

#content>.container>#containerHeaderRow>div:nth-child(5){
    display: grid;
    place-items: center;
}

#content>.container>.item:hover #due{
    background-color: var(--secondary-color-darker);
}

#due{
    border: none;
}

#content>.container>.item>.checkboxDiv{
    display: grid;
    place-items: center;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAEA;IACI,sBAAsB;IACtB,gCAAgC;IAChC,uCAAuC;IACvC,wBAAwB;IACxB,2CAA2C;IAC3C,wBAAwB;IACxB,4CAA4C;AAChD;;AAEA;IACI,aAAa;IACb,gCAAgC;IAChC,uBAAuB;IACvB,aAAa;IACb,SAAS;IACT,iCAAiC;AACrC;;AAEA;IACI,aAAa;IACb,4BAA4B;IAC5B,uCAAuC;IACvC,yBAAyB;IACzB,qBAAqB;IACrB,cAAc;IACd,mCAAmC;IACnC,yCAAyC;IACzC,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,mBAAmB;IACnB,kBAAkB;IAClB,mCAAmC;IACnC,YAAY;IACZ,8CAA8C;IAC9C,+CAA+C;IAC/C,YAAY;AAChB;;AAEA;IACI,gBAAgB;IAChB,gBAAgB;IAChB,6BAA6B;IAC7B,iBAAiB;AACrB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,YAAY;IACZ,YAAY;IACZ,gBAAgB;IAChB,mCAAmC;IACnC,6BAA6B;IAC7B,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,4CAA4C;IAC5C,0CAA0C;AAC9C;;;;AAIA;IACI,iBAAiB;AACrB;;;;AAIA;IACI,aAAa;IACb,0BAA0B;IAC1B,oCAAoC;IACpC,cAAc;IACd,mCAAmC;IACnC,UAAU;IACV,aAAa;IACb,aAAa;AACjB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,uBAAuB;IACvB,yBAAyB;IACzB,aAAa;IACb,qBAAqB;IACrB,mBAAmB;IACnB,iCAAiC;IACjC,wCAAwC;;AAE5C;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,uBAAuB;IACvB,+BAA+B;IAC/B,wCAAwC;AAC5C;;AAEA;;IAEI,aAAa;IACb,2CAA2C;IAC3C,wBAAwB;IACxB,YAAY;IACZ,mBAAmB;IACnB,0CAA0C;AAC9C;;AAEA;IACI,YAAY;IACZ,wCAAwC;IACxC,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,2BAA2B;IAC3B,4BAA4B;AAChC;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,+CAA+C;IAC/C,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,oCAAoC;AACxC;;AAEA;;IAEI,yCAAyC;IACzC,sBAAsB;AAC1B;;AAEA;;IAEI,yCAAyC;IACzC,sBAAsB;AAC1B;;AAEA;;IAEI,yCAAyC;IACzC,sBAAsB;AAC1B;;AAEA;;IAEI,yCAAyC;IACzC,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,+CAA+C;AACnD;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');\r\n\r\n:root {\r\n    box-sizing: border-box;\r\n    --main-color: rgb(151, 191, 255);\r\n    --main-color-darker: rgb(120, 172, 255);\r\n    --font-color-menu: 